import React from 'react';

import laravel_log from './../../assets/images/laravel-logo.png';
import php_logo from './../../assets/images/php-logo.png';
import symfony_logo from './../../assets/images/symfony-logo.png';

const datas = [
    {
        title : "Gestion de vente de lunette",
        descritpion: " C'est une application fait à partir de PHP, gerant la liste de stock de produit présantant des graphes de produit rapport vente",
        date : "2017",
        logo : php_logo
    },
    {
        title : "E-commerce",
        descritpion: "Site e-commerce, site web qui vent des produit des autres boutiques",
        date : "2020",
        logo : laravel_log
    },
    {
        title : "Hotel booking",
        descritpion: "Une site d'hôtel affichants la liste des chambres disponibles avec son spécificité. Via le site on peut faire des réservation de chambre. Le site a été fait avec le farmework Laravel 5",
        date : "2018",
        logo : symfony_logo
    },
];


class ProjectList extends React.Component 
{

    render() {
            return  (
                <div className="container">
                <section className="media row">
                    <nav className="title col-md-12">
                        <div className="row">
                            <div className="col-md-12">
                                <h1>Mes réalisations</h1>
                            </div>
                        </div>
                    </nav>
                    <div className="col-md-12">
                        <div className="row">
                        {datas ? datas.map((item, index) => {
                            return (<div key={index} className="col-md-4 col-xs-12 col-sm-6">
                                <div className="media-col">
                                    <div className="media-object php-image">
                                        <img src={item.logo} alt="" className="img-thumbnail" />
                                    </div>
                                    <div className="media-body">
                                        <h2>{item.title}</h2>
                                        <p>Date : {item.date}</p>
                                        <p>{item.descritpion}</p>
                                    </div>
                                </div>
                            </div>)
                        }): null}

                        </div>
                    </div>
                </section>
        </div>
            )
    }
}

export default ProjectList;